:root {
	--primary-color: #326bb2;
	--secondary-color: #2f4984;
}
* {
	user-select: none;
	margin: 0;
	padding: 0;
}

@font-face {
	font-family: 'Montserrat';
	src: url('Montserrat-Regular.ttf');
}

body {
	font-family: 'Montserrat', sans-serif;
}

.container {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	min-width: 240px;
	height: 100vh;
}

.current-ticket-status {
	display: flex;
	flex-wrap: wrap;
}

.current-ticket-status > div {
	height: 40vh;
	flex: 50% 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-size: 80px;
	border-bottom: 1px solid transparent;
}

.ticket-number-wrapper {
	background-color: var(--primary-color);
	color: #fff;
}

.calling {
	animation: calling-animation .7s 4;
}

div.current-window-wrapper {
	border-bottom-color: var(--primary-color);
}

.window-label {
	color: var(--primary-color);
}

.window {
	color: #7f7f7f;
}

.history-wrapper {
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
	font-size: 30px;
	color: #7f7f7f;
}

.history-wrapper > div {
	height: 50vh;
	flex: 50% 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border-bottom: 1px solid transparent;
}

.history-title {
	text-align: center;
	margin: 20px 0;
}

.history-wrapper table {
	width: 100%;
	border-collapse: collapse;
}

.history-wrapper table th {
	font-weight: normal;
	color: var(--primary-color);
	padding-bottom: 15px;
}

.history-wrapper table tr:nth-child(even) {
	background-color: #dcdcdc;
}

.history-wrapper table td {
	text-align: center;
	padding: 10px 0;
}

div.distraction {
	display: none;
}

.distraction iframe {
	width: 100%;
	height: 50vh;
}

.time-and-news-wrapper {
	display: flex;
}

.time-and-data {
	flex: 1;
	background-color: var(--secondary-color);
	font-weight: bold;
}

.news-wrapper {
	background-color: var(--primary-color);
	flex: 5;
	justify-content: inherit;
	position: relative;
	overflow: hidden;
	list-style: none;
}

.time-and-news-wrapper .footer-item {
	height: 50px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	color: #fff;
	font-size: 25px;
}

.news-wrapper ul {
	list-style: none;
	left: 100%;
	position: absolute;
	white-space: nowrap;
	animation: sliding 50s linear infinite;
}

.news-wrapper ul .news-item {
	margin-right: 100vw;
}

.news-wrapper ul .news-item:last-child {
	margin-right: 0;
}

@keyframes calling-animation {
	51% {
		background-color: #4197ff;
	}

	100% {
		background-color: var(--primary-color);
	}
}

@keyframes sliding {
	100% {
		left: 0;
		transform: translateX(-100%);
	}
}

@media screen and (max-width: 668px) {
	div.ticket-number-wrapper {
		height: 30vh;
		font-size: 70px;
	}
	div.current-window-wrapper {
		height: 30vh;
		font-size: 70px;
	}
}

@media screen and (max-width: 372px) {
	div.history-wrapper {
		font-size: 18px;
	}
}
@media screen and (max-width: 310px) {
	div.ticket-number-wrapper {
		font-size: 60px;
	}
	div.current-window-wrapper {
		font-size: 60px;
	}
}
